* {
    margin: 0;
    padding: 0;
    color: #ffffff;
}

.mainTitle {
    text-align: center;
    margin: 50px auto;
    font-size: 20px;
}

body {
    background: rgb(19, 19, 19);
}

a:link,
a:visited {
    color: #191919;
    text-decoration: none;
}

.koko {
    font-size: 40px;
    width: 100%;
    height: 100vh;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.wrap {
    display: flex;
    flex-wrap: wrap;
    gap: 5px 5%;
    justify-content: center;
    width: 70%;
    margin:auto;
}

.wrap>div {
    width: 20%;
}

.wrap img {
    width: 100%;
}

