li,
ul {
    list-style: none;
}


.title {
    width: 100%;
    font-size: 18px;
    text-align:left;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    padding: 3px 10px;
    box-sizing: border-box;
    border-left: 3px solid rgb(215, 193, 49);
    margin-top: 5px;
}

.mbox {
    display: flex;
    flex-wrap: wrap;
}

.mbox>div {
    width: 100%;
    box-sizing: border-box;
    overflow: hidden;
    margin-bottom: 30px;
}

.catagory {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
    justify-content: left;
}

.catetotal>p {
    font-size: 20px;
    margin: 10px 0px;
}

.catagory>li {
    background: rgb(188, 37, 90);
    border-radius: 4px;
    width: 40%;
    text-align: center;
    padding: 3px 0px;
    display: none;
}

.catagory>li:first-child,
.catagory>li:nth-child(2) {
    display: block;
}

.more {
    text-align: center;
    width: 150px;
    background: rgb(215, 193, 49);
    margin: 10px auto;
    padding: 10px 0px;
    border-radius: 9px;
}

.more a {
    color: #fff;
    width: 100%;
    display: inline-block;
    height: 100%;
}