@font-face {
    font-family: 'SeoulHangangM';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_two@1.0/SeoulHangangM.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

* {
    font-family: 'SeoulHangangM';
    margin: 0;
    padding: 0;
    color: #ffffff;
}

.mainTitle {
    text-align: center;
    margin: 50px auto;
    font-size: 20px;
}

body {
    background: rgb(19, 19, 19);
}

a:link,
a:visited {
    color: #191919;
    text-decoration: none;
}

.koko {
    font-size: 40px;
    width: 100%;
    height: 100vh;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}


.backImg {
    width: 100%;
    height: 100vh;
    object-fit: cover;
    position: fixed;
    top: 0;
    z-index: -1;
}

.moviecover {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 60%;
    margin: 5% auto;
    background: rgba(255, 255, 255, 0.63);
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 10px 10px 10px #000000;
}

.moviecover>div {
    width: 50%;
    text-align: left;
    box-sizing: border-box;
}
.textbox{padding-top: 5%;}
.textbox *{color: #191919;}

.moviecover img {
    display: block;
}
.title1{font-size: 25px; line-height: 1.3; font-weight: 100;}
.title1>b{font-size: 30px;}
.year{font-size: 50px; font-weight: 600; margin: 20px auto; color: tomato;}